.backgroundModal {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .5);
    padding: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modalCard {
    width: fit-content;
    height: fit-content;
    background: #fff;
    color: var(--gray2);
    border-radius: 5PX;
    position: relative;
    box-shadow: rgba(100, 100, 111, .2) 0px 7px 29px 0px;
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.modalCard p {
    margin: 1rem 0;
}

.buttonModal {
    background: var(--orange2);
    border: none;
    color: var(--white);
}